.coinCardBody {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0px 50px;
    box-sizing: border-box;
    border-bottom: 1px solid #9FA5BE;
    background: #F4F5F9;
    transition: 0.12s ease-out;
    font-family: Cera;
    cursor: pointer;
}

.coinCardBody:hover {
    background: #fff;
}

.coin {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
    flex: 0 0 15%;
    padding: 0px 20px;
}

.price, .dailyChange, .dailyVolume, .marketCap {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex: 0 0 10%;
    padding: 0px 20px;
}

.rank {
    flex: 0 0 5%;
    padding: 0px 20px;
}

.chart {
    width: 10%;
    padding: 5px 0px;
    min-width: 100px;
}

.coinCardBody p {
    font-size: 15px;
}

.coinIcon {
    width: 24px;
}